import * as React from 'react';
import { RenderToolbar, ItemKey, ItemSlot, SlotType } from '@ali/4ever-component';
import HighlightButton from './components/next/highlightButton';

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.highlight) {
      return (
        <ItemSlot display={SlotType.splitButton}>
          <HighlightButton
            controller={controller}
            locale={locale}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    return null
  };
}
